.card{
    border-radius: 5px;
    -webkit-box-shadow: 0 0 3px 3px rgba(0,0,0,0.05);
            box-shadow: 0 0 3px 3px rgba(0,0,0,0.05);
    padding: 20px;
    border: 1px solid rgba(0,0,0,0.1);
    display: inline-block;
    width: 200px;
    text-align: center;
    height: 250px;
    margin-top: 50px;
    margin-right: 30px;
    vertical-align: top;
    -webkit-transition: width 0.4s ease-in-out, height 0.4s ease-in-out, margin-top 0.4s ease-in-out;
    -o-transition: width 0.4s ease-in-out, height 0.4s ease-in-out, margin-top 0.4s ease-in-out;
    transition: width 0.4s ease-in-out, height 0.4s ease-in-out, margin-top 0.4s ease-in-out;
}
.card-central{
    width: 500px;
    height: 300px;
    margin-top: 0;
    margin-left: 20px;
    margin-right: 50px;
}
.cardsWrapper{
    width: 99999px;
    position: absolute;
    -webkit-transition: left 0.4s ease-in-out;
    -o-transition: left 0.4s ease-in-out;
    transition: left 0.4s ease-in-out;
}
.sliderWrapper{
    text-align: left;
    position: relative;
}
.rightArrow{
    cursor: pointer;
    top: 220px;
    left: calc(50vw + 255px);;
    width: 80px;
    position: absolute;
}
.leftArrow{
    cursor: pointer;
    top: 220px;
    left: calc(50vw - 330px);
    width: 80px;
    position: absolute;
    -webkit-transform-origin: 50% 50%;
        -ms-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
}
.topComparisonButton{
    position: absolute;
    width: 400px;
    left: calc(50vw - 200px);
    top: 0;
}
.bottomComparisonButton{
    position: absolute;
    width: 400px;
    left: calc(50vw - 200px);
    bottom: 0;
}
.SliderBigWrapper{
    overflow: hidden;
    width: 100%;
    height: 460px;
    position: relative;
}
.SlidersContainer{
    textAlign: center;
    position: absolute;
    -webkit-transition: top 0.4s ease-in-out;
    -o-transition: top 0.4s ease-in-out;
    transition: top 0.4s ease-in-out;
}
.simpleSelect{
    border: none;
    background: transparent;
    text-decoration: underline;
    padding: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 0;
    -o-text-overflow: '';
       text-overflow: '';
    text-align: center;
}
select::-ms-expand {
    display: none;
}
select:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 #000;
}
*{
    outline: none !important;
}
select:focus {
    border: none !important;
    outline: 0;
}
.simpleInput{
    border: none !important;
    background: transparent !important;
    text-align: center;
    text-decoration: underline;
    width: 80px;
}


